<template>
  <div class="wrapper">
    <side-bar
      type="sidebar" 
      :sidebar-links="$sidebar.sidebarLinks" 
    />
    <notifications />
    <div class="main-panel">
      <top-navbar />
      <dashboard-content
        @click.native="toggleSidebar"
      />
      <content-footer />
    </div>
  </div>
</template>

<script>
  export default {
    components: {
      TopNavbar: () => import('./TopNavbar.vue'),
      ContentFooter: () => import('./ContentFooter.vue'),
      DashboardContent: () => import('./Content.vue')
    },
    watch: {
      $route(newRoute, oldRoute) {
        this.toggleSidebar()
      }
    },
    methods: {
      toggleSidebar () {
        if (this.$sidebar.showSidebar) {
          this.$sidebar.displaySidebar(false)
        }
      }
    }
  }

</script>



